////! Component
export class Component {
    constructor(payload = {}) {
        const { tagName = 'div', state={}, props={} } = payload
        this.el = document.createElement(tagName)
        this.state = state
        this.props = props
        this.render()
    }
    render() {
    }
}

////! Router
function routeRender(routes) {
    if (!location.hash) {
        // *ÌûàÏä§ÌÜ†Î¶¨ ÎÇ¥Ïó≠Ïóê Í∏∞Î°ùÏùÑ ÎÇ®Í∏∞ÏßÄ ÏïäÏúºÎ©¥ÏÑú ÌéòÏù¥ÏßÄ Ïù¥Îèô
        history.replaceState(null, '', '/#/')
    }

    const routerView = document.querySelector('router-view')
    const [hash, queryString = ''] = location.hash.split('?')

    //? 1. ÏøºÎ¶¨ Ïä§Ìä∏ÎßÅÏùÑ Í∞ùÏ≤¥Î°ú Î≥ÄÌôòÌïòÏó¨ ÌûàÏä§ÌÜ†Î¶¨Ïùò ÏÉÅÌÉúÏóê Ï†ÄÏû• 
    const query = queryString
    .split('&')
    .reduce((acc, cur) => {
        const [key, value] = cur.split('=')
        acc[key] = value
        return acc
    },{})
    history.replaceState(query, '')

    //? 2. ÌòÑÏû¨ ÎùºÏö∞Ìä∏ Ï†ïÎ≥¥Î•º Ï∞æÏïÑÏÑú Î†åÎçîÎßÅ
    const currentRoute = routes.find(route => new RegExp(`${route.path}/?$`).test(hash))
    routerView.innerHTML = ''
    routerView.append(new currentRoute.component().el)

    //? 3. ÌôîÎ©¥ Ï∂úÎ†• ÌõÑ Ïä§ÌÅ¨Î°§ ÏúÑÏπò Î≥µÍµ¨
    window.scrollTo(0,0)
}
export function createRouter(routes){
    return function () {
        window.addEventListener('popstate', () => {
            routeRender(routes)
        })
        routeRender(routes)
    }
}

////! Store
export class Store {
    constructor(state) {
        this.state = {}
        this.observers = {}

        for (const key in state) {
            // *Í∞ùÏ≤¥ Îç∞Ïù¥ÌÑ∞Ïùò ÏÜçÏÑ±ÏùÑ Ï†ïÏùòÌïòÎäî Î©îÏÜåÎìú
            Object.defineProperty(this.state, key, {
                get: () => state[key],
                set: val => {
                    state[key] = val
                    // Î∞∞Ïó¥ Îç∞Ïù¥ÌÑ∞Î°ú ÎêòÏñ¥ÏûàÎäî ÏΩúÎ∞± Ìï®ÏàòÎì§ÏùÑ Ï∞®Î°ÄÎåÄÎ°ú Ïã§Ìñâ
                    // this.observers[key].forEach(observer => observer(val))
                    if (Array.isArray(this.observers[key])) {
                        this.observers[key].forEach(observer => observer(val));
                    }
                }
            })
        }
    }
    //* ÏÉÅÌÉúÎ•º Í∞êÏãúÌïòÎäî Î©îÏÜåÎìú
    subscribe(key, cb) {
        // Î∞∞Ïó¥ Îç∞Ïù¥ÌÑ∞Î•º Ïù¥Ïö©ÌïòÏó¨ Ïã§Ìñâ Ìï®ÏàòÎ•º Ìïú Í∞ú Ïù¥ÏÉÅ Îì±Î°ùÌï† Ïàò ÏûàÎèÑÎ°ù Íµ¨ÌòÑ
        // Î∞∞Ïó¥ Îç∞Ïù¥ÌÑ∞Ïù¥Î©¥ push Î©îÏÜåÎìú ÏÇ¨Ïö©
        // this.observers ==> { key: [cb1, cb2, cb3] } Ïùò ÌòïÏãùÏùÑ Í∞ÄÏßÑÎã§.
        // Array.isArray(this.observers[key])
        // ? this.observers[key].push(cb)
        // : this.observers[key] = [cb]
        if (!Array.isArray(this.observers[key])) {
            this.observers[key] = []; // Ï¥àÍ∏∞Ìôî
        }
        this.observers[key].push(cb);
    }
}